import React from "react";
import { Link } from "react-router-dom";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";

class UnplayedGame extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      doc: "",
      questions: null
    };

    this.getGameById = this.getGameById.bind(this);
  }

  componentDidMount = () => {
    this.getGameById();
  };

  getGameById = () => {
    this.props.dbGame
      .find({
        selector: {
          _id: this.props._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          doc: rows
        });
        console.log(this.state.doc);
        console.log(this.props._id);
      })
      .catch(err => {
        console.log(err);
      });
    this.getQuestions();
  };

  getQuestions = () => {
    this.props.dbQuestion
      .find({
        selector: {
          parentGame: this.props._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          questions: rows
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  timeEstimation() {
    return (
      Number(this.state.doc[0].timePerQuestion) *
      Number(this.state.questions ? this.state.questions.length : 0)
    );
  }

  totalScore() {
    return (
      100 * Number(this.state.questions > 0 ? this.state.questions.length : 0)
    );
  }

  render() {
    if (this.state.doc === "") {
      return null;
    } else {
      return (
        <div>
          <Link
            to={"/play/" + this.props._id}
            style={{ textDecoration: "none" }}
          >
            <div className="card">
              <div className="card-stats">
                <h3 style={{ color: this.props.color }}>
                  {this.state.doc[0].name}
                </h3>
                <h3>{this.timeEstimation()}</h3>
                <h3>
                  {this.state.questions > 0 ? this.state.questions.length : 0}
                </h3>
                <h3>{this.totalScore()}</h3>
              </div>
            </div>
          </Link>
        </div>
      );
    }
  }
}

export default UnplayedGame;
