import React, { Component } from "react";
import "./stylesheets/App.css";

class Logo extends Component {
  state = {
    selectedFile: null,
    edit: false
  };

  componentDidMount() {
    this.fetchClient();
  }

  fetchClient = () => {
    this.props.dbClient
      .find({
        selector: {
          _id: this.props.client
        }
      })
      .then(res => {
        const rows = res.docs[0];
        console.log(rows);
      })
      .catch(err => {
        console.log(err);
      });
  };

  fileSelectedhandler = event => {
    this.setState({
      selectedFile: event.target.files[0]
    });
  };

  fileUploadHandler = () => {
    const c = this.props.dbClient;
    const d = this.props.client;
    // post to couch
    const fd = this.state.selectedFile;
    c.get(d)
      .then(function(doc) {
        doc.logo = fd;
        return c.put(doc);
      })
      .then(function() {
        // fetch data again
        return c.get(d);
      })
      .then(function(doc) {
        console.log(doc);
      });
  };

  render() {
    return (
      <div className="logoDisplay">
        <div className="theme-edit-button">
          {this.state.edit === false ? (
            <button
              onClick={() => {
                this.fileInput.click();
                this.setState({
                  edit: true
                });
              }}
            >
              <img src={require("./images/edit-icon.png")} alt="edit icon" />
            </button>
          ) : (
            <button onClick={this.fileUploadHandler}>Save</button>
          )}
        </div>
        <h3>Logo</h3>
        <input
          type="file"
          style={{ display: "none" }}
          onChange={this.fileSelectedhandler}
          ref={fileInput => (this.fileInput = fileInput)}
        />
        <img src={this.props.headerImage} alt="logo" height={70} />
      </div>
    );
  }
}

export default Logo;
