import React from "react";
import { Link } from "react-router-dom";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";

class TeamUserCard extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      doc: "",
      questions: null,
      team: "",
      games: null
    };

    this.getUserById = this.getUserById.bind(this);
  }

  componentDidMount = () => {
    this.getUserById();
    this.getGames();
  };

  getUserById = () => {
    this.props.dbUser
      .find({
        selector: {
          _id: this.props.user._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          doc: rows
        });
      })
      .catch(err => {
        console.log(err);
      });
    this.fetchTeam();
  };

  fetchTeam = () => {
    this.props.dbTeam
      .find({
        selector: {
          _id: this.props.user.parentTeam
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          team: rows
        });

        console.log(rows);
      })
      .catch(err => {
        console.log(err);
      });
  };

  getGames = () => {
    this.props.dbGame
      .find({
        selector: {
          parentTeam: this.props.user.parentTeam
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          games: rows
        });
        console.log(this.state.games);
      })
      .catch(err => {
        console.log(err);
      });
  };

  render() {
    if (this.state.doc === "") {
      return null;
    } else {
      return (
        <div>
          <Link
            to={"/profile/" + this.props.user._id}
            style={{ textDecoration: "none" }}
          >
            <div className="card">
              <div className="card-stats">
                <h3
                  style={{
                    color: this.props.color,
                    fontSize: "26px",
                    fontWeight: "500"
                  }}
                >
                  {this.props.user.firstname} {this.props.user.lastname}
                </h3>
                <h3>Hardcode</h3>
                <h3>Hardcode</h3>
                <h3>Hardcode</h3>
                <h3>{this.state.games ? this.state.games.length : 0}</h3>
              </div>
            </div>
          </Link>
        </div>
      );
    }
  }
}

export default TeamUserCard;
