import React from "react";
import { Link } from "react-router-dom";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";

class TeamCard extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      doc: "",
      games: null,
      users: null,
      team: ""
    };

    this.getTeamById = this.getTeamById.bind(this);
  }

  componentDidMount = () => {
    this.getTeamById();
    this.fetchGames();
    this.getUsers();
  };

  getTeamById = () => {
    this.props.dbTeam
      .find({
        selector: {
          _id: this.props.team._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          doc: rows
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  fetchGames = () => {
    this.props.dbGame
      .find({
        selector: {
          parentTeam: this.props.team._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          games: rows
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  getUsers = () => {
    this.props.dbUser
      .find({
        selector: {
          parentTeam: this.props.team._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          users: rows
        });
        console.log(rows);
      })
      .catch(err => {
        console.log(err);
      });
  };

  rating() {
    var count = 0;
    this.state.rating.map((rating, i) => {
      return (count += rating.rating);
    });
    if (this.state.rating.length === 0) {
      return 0;
    } else {
      return count / this.state.rating.length;
    }
  }

  timeEstimation() {
    return (
      Number(this.state.doc[0].timePerQuestion) *
      Number(this.state.questions ? this.state.questions.length : 0)
    );
  }

  render() {
    if (this.state.doc === "" || this.state.rating === null) {
      return null;
    } else {
      return (
        <div>
          <Link
            to={"/editTeam/games/" + this.props.team._id}
            style={{ textDecoration: "none" }}
          >
            <div className="card">
              <div className="card-stats">
                <h3
                  style={{
                    color: this.props.color,
                    fontSize: "26px",
                    fontWeight: "500"
                  }}
                >
                  {this.props.team.name}
                </h3>
                <h3></h3>
                <h3>{this.state.games ? this.state.games.length : 0}</h3>
                <h3>{this.state.users ? this.state.users.length : 0}</h3>
              </div>
            </div>
          </Link>
        </div>
      );
    }
  }
}

export default TeamCard;
