import React from "react";
import { FaStar } from "react-icons/fa";
import "./stylesheets/starRating.css";

const CardRating = props => {
  return (
    <div>
      {[...Array(5)].map((star, i) => {
        const ratingValue = i + 1;
        return (
          <label key={i}>
            <FaStar
              className="star"
              color={ratingValue <= props.rating ? props.color : "#e4e5e9"}
              size={30}
            />
          </label>
        );
      })}
    </div>
  );
};

export default CardRating;
