import React from "react";
import { Link } from "react-router-dom";
import CardRating from "./cardRating";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";

class GameCard extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      doc: "",
      questions: null,
      rating: null,
      team: ""
    };

    this.getGameById = this.getGameById.bind(this);
  }

  componentDidMount = () => {
    this.getGameById();
    this.getRating();
    this.getQuestions();
  };

  getGameById = () => {
    this.props.dbGame
      .find({
        selector: {
          _id: this.props.game._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          doc: rows
        });
      })
      .catch(err => {
        console.log(err);
      });
    this.fetchTeam();
  };

  fetchTeam = () => {
    this.props.dbTeam
      .find({
        selector: {
          _id: this.props.game.parentTeam
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          team: rows.name
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  getRating = () => {
    this.props.dbRating
      .find({
        selector: {
          game: this.props.game._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          rating: rows
        });
        console.log(rows);
      })
      .catch(err => {
        console.log(err);
      });
  };

  getQuestions = () => {
    this.props.dbQuestion
      .find({
        selector: {
          parentGame: this.props.game._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          questions: rows
        });
        console.log(rows);
      })
      .catch(err => {
        console.log(err);
      });
  };

  rating() {
    var count = 0;
    this.state.rating.map((rating, i) => {
      return (count += rating.rating);
    });
    if (this.state.rating.length === 0) {
      return 0;
    } else {
      return count / this.state.rating.length;
    }
  }

  timeEstimation() {
    return (
      Number(this.state.doc[0].timePerQuestion) *
      Number(this.state.questions ? this.state.questions.length : 0)
    );
  }

  render() {
    if (this.state.doc === "" || this.state.rating === null) {
      return null;
    } else {
      return (
        <div>
          <Link
            to={"/editGame/" + this.props.game._id}
            style={{ textDecoration: "none" }}
          >
            <div className="card">
              <div className="card-stats">
                <h3
                  style={{
                    color: this.props.color,
                    fontSize: "26px",
                    fontWeight: "500"
                  }}
                >
                  {this.props.game.name}
                </h3>
                <h3>
                  {this.props.team ? (
                    this.state.team
                  ) : (
                    <CardRating
                      color={this.props.color}
                      rating={Math.floor(this.rating())}
                    />
                  )}
                </h3>
                <h3>
                  {this.state.questions ? this.state.questions.length : 0}
                </h3>
                <h3>{this.timeEstimation()}</h3>
              </div>
            </div>
          </Link>
        </div>
      );
    }
  }
}

export default GameCard;
